import React from 'react'

const PlanCard = ({ name, level = '', specs = [], link, price, discount, tag }) => {
    if (!specs.length) {
        specs = [{ text: '' }, { text: '' }, { text: '' }]
    }
    let pricePeriod = tag == 'child' ? '':''
    let levelTag = { 'base': '', 'advanced': '', 'expert': ' ' }[level]
    const priceLabel = (<> <strong>{Math.round(price * (100 - discount) / 100).toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")}</strong> /{pricePeriod}</>)
    const priceDiscountLabel = discount ? (<span className='plan__card_discount' ><strong className='plan__card_price-discount'>{Math.round(price).toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")}</strong> /{pricePeriod}</span>) : ''
    return (
        <div className='plan__card'>
            <h3 className="plan__card_name">{name}</h3>
            <ul className="plan__card_descriptions">
                {specs.map((specs) =>
                    <li className="plan__card_description">{specs.text}</li>
                )}
            </ul>
            <p className="plan__price">{priceLabel} {priceDiscountLabel}</p>
            <a href={link} className="plan__card_link">  </a>
            <div className={`plan__card_level ${level}`}>{levelTag}</div>
        </div>
    )
}

export default PlanCard